*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
}

.Board {
    user-select: none;
}

.Board__row {
    display: flex;
}

.Cell {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Menu {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 1rem;
    background-color: #4a752c;
    width: 100%;
}

.Menu__select {
    border: none;
    background: transparent;
    cursor: pointer;
}

.EndGame {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.EndGame__button {
    background-color: rgb(162 210 73 / 0.6);
    padding: 1.5rem 3rem;
    border-radius: 0.35rem;
    font-size: 1.5rem;
    cursor: pointer;
    border: none;
    transition: 1s ease;
}

.EndGame__button:hover {
    background-color: rgb(209 252 122 / 0.5);
}
